import {Component, Input} from '@angular/core';
import { TopNavigaitonMenuComponent } from '../top navigation menu/top_nav_menu.component';
import {User} from '../authorization/user';
import {UserService} from '../authorization/services/user.service';

@Component({
    selector: 'side-menu',
    template: require('./side-menu.html'),
    styles: [require('./side-menu.style.css')],
    providers: [UserService]
})

export class SideMenuComponent {          
    constructor(private _userService: UserService){
    }

    toggleClass(element, className){
        element.classList.toggle(className);
    }

    isUserLoggedIn(): boolean{
        return this._userService.isLoggedIn();
    }

    isUserInRole(roles: Array<string>): boolean{
        return this._userService.isInRole(roles);
    }
}


