/*
 * Angular 2 decorators and services
 */
import { Component, ViewEncapsulation } from '@angular/core';
import {EmployeeListComponent} from "./core/employee/employee-list.component";
import { TopNavigaitonMenuComponent } from './common/top navigation menu/top_nav_menu.component';
import {SideMenuComponent} from "./common/side-menu/side-menu.component";
import { BreadcrumbComponent } from './common/breadcrumb/breadcrumb.component';
import { PageHeaderComponent } from './common/page header/page-header.component';
import { FooterComponent } from './common/footer/footer.component';
import { QuickActionsMenuComponent } from './common/quick-actions-menu/quick-actions-menu.component';
import { ROUTER_DIRECTIVES } from '@angular/router';
import { ViewContainerRef} from '@angular/core';

import { OAuthService } from 'angular2-oauth2/oauth-service';
import {Http, Headers, URLSearchParams, RequestOptions} from '@angular/http';
import {APP_BASE_HREF} from "@angular/common";

require('../assets/js/libs/jquery.min.js');
//require('style-loader!css-loader!font-awesome/css/font-awesome.css?');

@Component({
    selector: 'app',
    template: require('./app.component.html'),
    //encapsulation: ViewEncapsulation.None,
    directives: [ROUTER_DIRECTIVES, TopNavigaitonMenuComponent, SideMenuComponent,  BreadcrumbComponent, 
    PageHeaderComponent, FooterComponent, QuickActionsMenuComponent],
    providers: [OAuthService, {
      provide: APP_BASE_HREF,
      useValue: '/'
    }]
})

export class AppComponent {

    public constructor(viewContainerRef:ViewContainerRef,
        private oauthService: OAuthService,
        private _http: Http) {
        this.viewContainerRef = viewContainerRef;

        // Login-Url
        this.oauthService.loginUrl = "https://hrtools.authorizationserver:44310/core/connect/authorize"; //Id-Provider?
        
        // URL of the SPA to redirect the user to after login
        this.oauthService.redirectUri = window.location.origin;// + "/index.html";
        
        // The SPA's id. Register SPA with this id at the auth-server
        this.oauthService.clientId = "public";
        
        // The name of the auth-server that has to be mentioned within the token
        this.oauthService.issuer = "https://hrtools.authorizationserver:44310/core";
 
        // set the scope for the permissions the client should request
        this.oauthService.scope = "openid profile email roles publicApi";
        
        // set to true, to receive also an id_token via OpenId Connect (OIDC) in addition to the
        // OAuth2-based access_token
        this.oauthService.oidc = true;

        // Use setStorage to use sessionStorage or another implementation of the TS-type Storage
        // instead of localStorage
        //this.oauthService.setStorage(sessionStorage);
        
        // To also enable single-sign-out set the url for your auth-server's logout-endpoint here
        this.oauthService.logoutUrl = "https://hrtools.authorizationserver:44310/core/connect/endsession?id_token={{id_token}}";
        
        // This method just tries to parse the token within the url when
        // the auth-server redirects the user back to the web-app
        // It dosn't initiate the login
        this.oauthService.tryLogin({});
        
        
        // TODO: validate token in callback

        // this.oauthService.tryLogin({
        //     addUserLoaded: any => {
        //         debugger;
        //     },
        //     onTokenReceived: context => {
        //         //
        //         // Output just for purpose of demonstration
        //         // Don't try this at home ... ;-)
        //         // 
        //         debugger;
        //         console.debug("logged in");
        //         console.debug(context);
        //     },
        //     validationHandler: context => {
        //         // debugger;
        //         // var search = new URLSearchParams();
        //         // search.set('token', context.idToken); 
        //         // search.set('client_id', oauthService.clientId);
        //         // return _http.get(validationUrl, { search}).toPromise();
        //         return null;// _http.get('https://localhost:3001', { }).toPromise();
        //     }
        // });//;);
    }

    ngOnInit() {
        setTimeout(() => {
            /*****
* CONFIGURATION
*/
            // Active ajax page loader
            // $.ajaxLoad = false;

            // //required when $.ajaxLoad = true
            // $.defaultPage = 'main.html';
            // $.subPagesDirectory = 'views/';
            // $.page404 = 'pages/404.html';
            // $.mainContent = $('#ui-view');

            //Main navigation
            $.navigation = $('nav > ul.nav');

            //Dynamic Title only if $.ajaxLoad = true
            $.dynamicTitle = true;

            //Dynamic Breadcrumb only if $.ajaxLoad = true
            $.dynamicBreadcrumb = true;

            $.panelIconOpened = 'icon-arrow-up';
            $.panelIconClosed = 'icon-arrow-down';

            //Default colours
            $.brandPrimary = '#20a8d8';
            $.brandSuccess = '#4dbd74';
            $.brandInfo = '#63c2de';
            $.brandWarning = '#f8cb00';
            $.brandDanger = '#f86c6b';

            $.grayDark = '#2a2c36';
            $.gray = '#55595c';
            $.grayLight = '#818a91';
            $.grayLighter = '#d1d4d7';
            $.grayLightest = '#f8f9fa';

            /*****
            * ASYNC LOAD
            * Load JS files and CSS files asynchronously in ajax mode
            */
            function loadJS(jsFiles, pageScript) {

                for (i = 0; i < jsFiles.length; i++) {

                    var body = document.getElementsByTagName('body')[0];
                    var script = document.createElement('script');
                    script.type = 'text/javascript';
                    script.async = false;
                    script.src = jsFiles[i];
                    body.appendChild(script);
                }

                if (pageScript) {
                    var body = document.getElementsByTagName('body')[0];
                    var script = document.createElement('script');
                    script.type = 'text/javascript';
                    script.async = false;
                    script.src = pageScript;
                    body.appendChild(script);
                }

            }

            var cssArray = {};


            /****
            * AJAX LOAD
            * Load pages asynchronously in ajax mode
            */

                /* ---------- Main Menu Open/Close, Min/Full ---------- */
                $('.navbar-toggler').click(function () {

                    var bodyClass = localStorage.getItem('body-class');

                    if ($(this).hasClass('layout-toggler') && $('body').hasClass('sidebar-off-canvas')) {
                        $('body').toggleClass('sidebar-opened').parent().toggleClass('sidebar-opened');
                        //resize charts
                        //resizeBroadcast();

                    } else if ($(this).hasClass('layout-toggler') && ($('body').hasClass('sidebar-nav') || bodyClass == 'sidebar-nav')) {
                        $('body').toggleClass('sidebar-nav');
                        localStorage.setItem('body-class', 'sidebar-nav');
                        if (bodyClass == 'sidebar-nav') {
                            localStorage.clear();
                        }
                        //resize charts
                        //resizeBroadcast();
                    } else {
                        $('body').toggleClass('mobile-open');
                    }
                });

                $('.aside-toggle').click(function () {
                    $('body').toggleClass('aside-menu-open');

                    //resize charts
                    //resizeBroadcast();
                });

                $('.sidebar-close').click(function () {
                    $('body').toggleClass('sidebar-opened').parent().toggleClass('sidebar-opened');
                });

                /* ---------- Disable moving to top ---------- */
                $('a[href="#"][data-top!=true]').click(function (e) {
                    e.preventDefault();
                });

           // });

        }, 0);
    }
}


