import {Component, Input} from '@angular/core';
import { ROUTER_DIRECTIVES } from '@angular/router';
import {EmployeeService} from './services/employee.service';
import {EmployeeComponent} from './employee.component';
import {Employee} from './employee';
import {EmployeesFilter} from './employeesFilter';
import {EmployeeSettings} from './EmployeeSettings';
import {OperationResult} from "../../common/alerts/operationResult";
import { FaComponent } from 'angular2-fontawesome/components';

import { BreadcrumbComponent } from '../../common/breadcrumb/breadcrumb.component';
import { PageHeaderComponent } from '../../common/page header/page-header.component';

import { PaginationComponent } from 'ng2-bootstrap/ng2-bootstrap';

@Component({
  selector: 'employeeList',
  template: require('./employee-list.template.html'),
  directives: [EmployeeComponent,
    ...ROUTER_DIRECTIVES,
    BreadcrumbComponent,
    PageHeaderComponent,
    FaComponent,
    PaginationComponent
  ],
  styles: [require('./employee-list.style.css')],
  providers: [EmployeeService, EmployeesFilter]
})

export class EmployeeListComponent {
  employeeList = new Array<Employee>();
  selectedEmployee = new Employee();
  employees = new Array<Employee>();
  pageSettings: EmployeeSettings;
  private officeLocations;
  private cities;
  private filters: EmployeesFilter = new EmployeesFilter();
  private _operationResult = new OperationResult();

  constructor(private _employeeService: EmployeeService) {
    this.resetFilters();
    this.pageSettings = new EmployeeSettings(0, 1, 2, '', this.filters);

    this.employees = this.getAll(this.pageSettings);
    
    this._employeeService.getCountriesWithCities()
        .subscribe(response => {
              this.officeLocations = response;
              this.initFilters();
        }
        , error => this.handleError(error)); 
  }

  getAll(gridSettings: EmployeeSettings): Array<Employee> {
      this._employeeService.getAll(gridSettings)
            .subscribe(response =>  
            {
                this.employees = response.data;
                this.pageSettings.totalCount = response.totalCount
            }
            , error => this.handleError(error));

        return this.employees;
    }

    search(searchKeyword: string, filters: EmployeesFilter): void{
        this.pageSettings.searchKeyword = searchKeyword;
        this.pageSettings.employeeFilter = filters;

        this.getAll(this.pageSettings);
    }

    private initFilters(): void{
        this.filters.officeLocations = this.officeLocations;
        this.resetFilters();

        this.filters.officeLocations.push({
            Country: this.filters.allCountriesText,
            Cities: [this.filters.allCitiesText]
        });

        this.initCities(this.filters.country);
    }

    resetFilters(): void{
        this.filters.country = this.filters.allCountriesText;
        this.filters.city = this.filters.allCitiesText;
        this.filters.status = this.filters.statusList[0].id;
    }    

    private initCities(country:string): Array<string>{
      let cities = new Array<string>();
      this.cities = cities;
        if(!country){
            return;
        }else if(country == this.filters.allCountriesText)
        {
            this.cities.push(this.filters.allCitiesText);
            this.filters.city = this.filters.allCitiesText;

            return cities;
        }

        let selectedCountry = country;
        this.officeLocations.map(loc => {
            if(loc.Country == selectedCountry)
            {
                this.cities.push(this.filters.allCitiesText);
                Array.prototype.push.apply(cities, (loc.Cities));
            }
        });
    }

    onPageChanged(event){
        this.pageSettings.currentPage = event.page;
        
        this.getAll(this.pageSettings);
    }

     private handleError(error): void{
        let errorMessage: string = "Some error has occured";

        if(error.status == 401){
            errorMessage = "You don't have permissions to make a request. Please, contact your administrator.";
        }

        this.showResult(false, errorMessage, "error");
        console.log(error);
    }

    private showResult(wasOperationSucceed, message, className){
        this._operationResult = {
				showMessage: true,
				message: message,
                isSuccess: wasOperationSucceed,
                isError: !wasOperationSucceed,
                doFade: false
			};

            setTimeout(() => {
            	this._operationResult.doFade = true;
            }, 4000);
    }

}


