import {Component, OnInit} from '@angular/core';

import {UserService} from '../authorization/services/user.service';
import {User} from '../authorization/user';

import { OAuthService } from 'angular2-oauth2/oauth-service';

import {Http, Headers, URLSearchParams, RequestOptions} from '@angular/http';

@Component({
    selector: 'login',
    template: require('./login.template.html')//,
    //styles: [require('./login.style.css')]
   //,directives: [...ROUTER_DIRECTIVES]
   , providers: [UserService]
})

export class LoginComponent{
    private user: User = new User();

    constructor(private oAuthService: OAuthService,
        private _http: Http,
        private _userService: UserService) {

        if(!this.oAuthService.hasValidAccessToken())
        {
            //this.oAuthService.initImplicitFlow();
        }else
        {
            this.getUserDetails();     
        }

        this.oAuthService.callEventIfExists({onTokenReceived: this.onTokenReceived, scope: this});
    }

    private onTokenReceived(options, that){
        if(options.accessToken){
            that._userService.validateAccessToken(options.accessToken)
                .subscribe(response =>  
                {
                    this.user = new User();
                    this.user.name = response.name;
                    this.user.roles = response.role;

                    var data = JSON.stringify(this.user);
                    localStorage.setItem("userInfo", data);

                    return this.user;
                }
                , error => { 
                    // handle not valid token
                    that.login();
                 }); 
        }
    }


    private getUserDetails(): User{
        let userInfo: string = localStorage.getItem('userInfo');
        let user: User;
        
        if(userInfo){
            this.user = JSON.parse(userInfo);

            return user;
        }else{
            this._userService.getUserDetails()                
                .subscribe(response =>  
                {
                    this.user = response;

                    var data = JSON.stringify(this.user);
                    localStorage.setItem("userInfo", data);

                    return this.user;
                }
                , error => { debugger; });      
        }
    }

    
    public login() {
        this.oAuthService.initImplicitFlow();
    }
    
    public logoff() {
        this.oAuthService.logOut();

        localStorage.removeItem("userInfo");
    }

    public isUserLoggedIn(): boolean{
        return this._userService.isLoggedIn();
    }   
}


