import {Component, Input, ViewChild} from '@angular/core';
import {EmployeeService} from './services/employee.service';
import {Employee} from './employee';
import {MdButton} from '@angular2-material/button';
import { AlertComponent } from 'ng2-bootstrap/ng2-bootstrap';
import {EmployeeDetailsModalComponent} from './modals/details/details.component';
import { MODAL_DIRECTIVES } from 'ng2-bootstrap/components/modal';
import { BS_VIEW_PROVIDERS } from 'ng2-bootstrap';

@Component({
    selector: 'employee',
    template: require('./employee.template.html'),
    styles: [require('./employee.style.css')],
    directives: [MdButton, AlertComponent, EmployeeDetailsModalComponent,
        MODAL_DIRECTIVES],
    viewProviders: [BS_VIEW_PROVIDERS],
})

export class EmployeeComponent {
    @Input() employee;
    @ViewChild(EmployeeDetailsModalComponent) detailsModal: EmployeeDetailsModalComponent

    constructor() { }

    toggleClass(element, className) {
        element.classList.toggle(className);
    }

    showDetails() {
        this.detailsModal.showModal();
    }
}


