import {Component, Input, ViewChild} from '@angular/core';
import {Employee} from '../../employee';
import { BS_VIEW_PROVIDERS, ModalDirective } from 'ng2-bootstrap';
import { MODAL_DIRECTIVES } from 'ng2-bootstrap/components/modal';
import {CORE_DIRECTIVES} from '@angular/common';
import { FORM_DIRECTIVES, NgClass, NgStyle} from '@angular/common';
import { TAB_DIRECTIVES } from 'ng2-bootstrap/ng2-bootstrap';

@Component({
    selector: 'employee-details',
    template: require('./details.template.html'),
    directives: [
        MODAL_DIRECTIVES, CORE_DIRECTIVES,  
        NgClass, NgStyle, FORM_DIRECTIVES, TAB_DIRECTIVES],
    viewProviders: [BS_VIEW_PROVIDERS]
})

export class EmployeeDetailsModalComponent {
    @Input() employee:Employee;
    @ViewChild('detailsModal') detailsModal: ModalDirective;

    public showModal(): void {        
        this.detailsModal.show();
    }
}


